@font-face {
  font-weight: normal;
  font-family: "Visuelt Pro";
  font-style: normal;
  src: url("../fonts/VisueltPro-Regular.woff2") format("woff2"), url("../fonts/VisueltPro-Regular.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Visuelt Pro";
  font-style: normal;
  src: url("../fonts/VisueltPro-Bold.woff2") format("woff2"), url("../fonts/VisueltPro-Bold.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  word-break: break-word;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
  min-width: 360px;
  font-family: "Visuelt Pro", "Arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
  color: #808080;
  padding: 0;
  margin: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.page-main {
  flex: 1 0 auto;
}

.page-footer {
  flex: 0 0 auto;
}

.container {
  width: 360px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 720px) {
  .container {
    width: 720px;
    padding: 0 56px;
  }
}
@media (min-width: 1392px) {
  .container {
    width: 1392px;
  }
}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4755eb;
  border-radius: 16px;
  border-color: transparent;
  font-size: 14px;
  line-height: 19px;
  color: #ffffff;
}
.main-btn:hover {
  background-color: #7580f0;
}
.main-btn:active {
  background-color: #a3aaf5;
}

.main-btn__text {
  color: #ffffff;
  display: inline;
}

.main-btn--white {
  background-color: #ffffff;
  color: #4755eb;
}
.main-btn--white:hover {
  background-color: #fafafa;
}
.main-btn--white:active {
  color: #7580f0;
}

.text-field {
  padding-left: 16px;
  padding-right: 140px;
  border-radius: 16px;
  border-color: transparent;
  background-color: #fafafa;
  box-shadow: -2px 4px 20px rgba(25, 25, 25, 0.025);
  outline: none;
  font-family: "Visuelt Pro";
  font-size: 14px;
  line-height: 19px;
  color: #808080;
}

.text-field--gray-light {
  background-color: #f2f2f2;
}

